import * as TWEEN from '@tweenjs/tween.js'
import * as THREE from 'three'
// import store from '@/store'
import * as GEOM from '../GeomUtils'

class TweenAnimController {
    animationObjectCount: number
    requestId: number
    constructor() {
        this.animationObjectCount = 0
        this.requestId = -1
    }
    addTweenAnimObject() {
        if(this.animationObjectCount <= 0) {
            this.startAnim()
        }
        this.animationObjectCount ++
    }
    removeTweenAnimObject() {
        if (this.animationObjectCount <= 0) {
            return
        }
        if(this.animationObjectCount == 1) {
            this.stopAnim()
        }
        this.animationObjectCount --
    }
    loopAnim = () => {
        TWEEN.update()
        if (this.requestId >= 0) {
            this.requestId = window.requestAnimationFrame(this.loopAnim)
        }
    }
    startAnim = () => {
        if (this.requestId < 0) {
            this.requestId = window.requestAnimationFrame(this.loopAnim)
        }
    }
    stopAnim = () => {
        if (this.requestId >= 0) {
            window.cancelAnimationFrame(this.requestId)
            this.requestId = -1
        }
    }
    tweenNumber(
            fromValue: number,
            toValue: number,
            onUpdateFunc: (value: number) => any,
            onCompleteFunc: (value: number) => any,
            duration: number = 1000,
            easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
        ): void {
        const v = { a: fromValue }
        this.addTweenAnimObject()
        new TWEEN.Tween(v)
            .to({ a: toValue }, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(v.a))
            .onComplete(() => {
                this.removeTweenAnimObject()
                onCompleteFunc(v.a)
            }).start()
    }
    tweenVector2(
        fromValue: GEOM.Vector2,
        toValue: GEOM.Vector2,
        onUpdateFunc: (x: number, y: number) => any,
        onCompleteFunc: (x: number, y: number) => any,
        duration: number = 1000,
        easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
    ): void {
        const v1 = { x: fromValue.x, y: fromValue.y }
        const v2 = { x: toValue.x, y: toValue.y }
        this.addTweenAnimObject()
        new TWEEN.Tween(v1)
            .to(v2, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(v1.x, v1.y))
            .onComplete(() => {
                this.removeTweenAnimObject()
                onCompleteFunc(v1.x, v1.y)
            }).start()
    }
    tweenVector3(
        fromValue: GEOM.Vector3,
        toValue: GEOM.Vector3,
        onUpdateFunc: (x: number, y: number, z: number) => any,
        onCompleteFunc: (x: number, y: number, z: number) => any,
        duration: number = 1000,
        easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
    ): void {
        const v1 = { x: fromValue.x, y: fromValue.y, z: fromValue.z }
        const v2 = { x: toValue.x, y: toValue.y, z: toValue.z }
        this.addTweenAnimObject()
        new TWEEN.Tween(v1)
            .to(v2, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(v1.x, v1.y, v1.z))
            .onComplete(() => {
                this.removeTweenAnimObject()
                onCompleteFunc(v1.x, v1.y, v1.z)
            }).start()
    }
    tweenEuler(
        fromValue: THREE.Euler,
        toValue: THREE.Euler,
        onUpdateFunc: (eu: THREE.Euler) => any,
        onCompleteFunc: (eu: THREE.Euler) => any,
        duration: number = 1000,
        easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
    ): void {
        const v1 = { x: fromValue.x, y: fromValue.y, z: fromValue.z }
        const v2 = { x: toValue.x, y: toValue.y, z: toValue.z }
        this.addTweenAnimObject()
        new TWEEN.Tween(v1)
            .to(v2, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(new THREE.Euler(v1.x, v1.y, v1.z)))
            .onComplete(() => {
                this.removeTweenAnimObject()
                onCompleteFunc(new THREE.Euler(v1.x, v1.y, v1.z))
            }).start()
    }
    tweenThreeVector3(
        fromValue: THREE.Vector3,
        toValue: THREE.Vector3,
        onUpdateFunc: (eu: THREE.Vector3) => any,
        onCompleteFunc: (eu: THREE.Vector3) => any,
        duration: number = 1000,
        easing: (amount: number) => number = TWEEN.Easing.Quadratic.InOut
    ): void {
        const v1 = { x: fromValue.x, y: fromValue.y, z: fromValue.z }
        const v2 = { x: toValue.x, y: toValue.y, z: toValue.z }
        this.addTweenAnimObject()
        new TWEEN.Tween(v1)
            .to(v2, duration)
            .easing(easing)
            .onUpdate(() => onUpdateFunc(new THREE.Vector3(v1.x, v1.y, v1.z)))
            .onComplete(() => {
                this.removeTweenAnimObject()
                onCompleteFunc(new THREE.Vector3(v1.x, v1.y, v1.z))
            }).start()
    }
}

const controller = new TweenAnimController()

export { controller }