import * as THREE from 'three'
import * as TWEEN from '@tweenjs/tween.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { Stats } from 'three-stats'
// import { TDSLoader } from 'three/examples/jsm/loaders/TDSLoader'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
import { createMultiMaterialObject } from 'three/examples/jsm/utils/SceneUtils'
import { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer } from '../Three/CustomCSS3DRenderer'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { DampingObjectController, DampingVector3Controller } from '../Three/Utils'
// import { RefractionMesh, RefractionManager } from '../Three/Refraction2.0'
import { RefractionObject, RefractionManager } from '../ThreeRealisticRefraction/ThreeRealisticRefraction'
import { RoundedBoxGeometry } from 'three/examples/jsm/geometries/RoundedBoxGeometry'
import { controller } from '../AnimUtils/TweenUtils'
import * as PE from '../Three/ParticleEngine'
import { randn_bm } from '../Three/Utils'

class BackgroundManager {
    container: HTMLElement
    renderer: THREE.WebGLRenderer
    scene: THREE.Scene
    // composer: EffectComposer
    stats: Stats
    controls: OrbitControls

    // cursor position
    mousePosX: number
    mousePosY: number
    pointer: THREE.Vector2

    // camera
    camera: THREE.PerspectiveCamera
    // cameraZPosition: number
    // cameraLootAtController: DampingVector3Controller
    // cameraTargetPosition: THREE.Vector3
    // cameraWithMouseRotateFactor: number
    // cameraWithMouseMoveFactor: number

    // loading manager
    loadingManager: THREE.LoadingManager

    constructor(_container: HTMLElement,
        onLoad?: () => void,
        onProgress?: (url: string, loaded: number, total: number) => void,
        onError?: (url: string) => void
        ) {
        const self = this

        // *********** basic settings ***********
        // this.cameraZPosition = 500
        this.container = _container
        this.camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
        this.renderer = new THREE.WebGLRenderer( {alpha: true} )
        this.renderer.outputEncoding = THREE.sRGBEncoding
        this.renderer.toneMappingExposure = THREE.ACESFilmicToneMapping
        this.renderer.toneMappingExposure = 1.2
        this.renderer.setClearColor( 0xffffff )
        this.renderer.setPixelRatio( window.devicePixelRatio )
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        this.renderer.domElement.style.position = 'absolute'
        this.renderer.domElement.style.top = '0px'
        this.renderer.domElement.style.left = '0px'
        this.container.appendChild( this.renderer.domElement )
        // TODO: (debug stats)
        this.stats = new Stats()
        this.stats.domElement.style.zIndex = '100'
        this.container.appendChild( this.stats.domElement )
        this.controls = new OrbitControls(this.camera, this.container)
        this.camera.position.set(0, 200, 150)

        this.scene = new THREE.Scene()

        // 
        this.loadingManager = new THREE.LoadingManager(() => {
            if (onLoad) {
                onLoad()
            }
        }, (url: string, loaded: number, total: number) => {
            if (onProgress) {
                onProgress(url, loaded, total)
            }
        }, (url) => {
            if (onError) {
                onError(url)
            }
        })

        // *********** background & env ***********
        const png_urls = [
            'px.png', 'nx.png',
            'py.png', 'ny.png',
            'pz.png', 'nz.png'
        ]
        const jpg_urls = [
            'px.jpg', 'nx.jpg',
            'py.jpg', 'ny.jpg',
            'pz.jpg', 'nz.jpg'
        ]
        var urls = this.getCubemapUrls('./cube2/', png_urls)
        const textureCube1 = new THREE.CubeTextureLoader(this.loadingManager).load( urls )
        textureCube1.mapping = THREE.CubeRefractionMapping
        // this.scene.background = textureCube
        this.scene.environment = textureCube1

        urls = this.getCubemapUrls('./bg/', png_urls)
        const textureCube2 = new THREE.CubeTextureLoader(this.loadingManager).load( urls )
        // textureCube2.mapping = THREE.CubeRefractionMapping
        this.scene.background = textureCube2



        const light = new THREE.DirectionalLight(0xffd060, 1)
        light.position.set(10, 10, 0)
        this.scene.add(light)
        const light1 = new THREE.DirectionalLight(0x60d0ff, 1)
        light1.position.set(-10, 10, 0)
        this.scene.add(light1)
        const light2 = new THREE.PointLight(0x60d0ff, 1)
        light2.position.set(0, 100, 0)
        this.scene.add(light2)


        // generate sphere array
        const sinPeriods = 3
        const sinAmplitude = 10
        const sinRadius1 = 120
        const sinRadius2 = 80
        const ps = []
        for (let i = 0; i < sinPeriods; i++) {
            ps.push(new THREE.Vector3(
                sinAmplitude / 2,
                Math.cos(Math.PI * 2 * i / sinPeriods) * sinRadius1,
                Math.sin(Math.PI * 2 * i / sinPeriods) * sinRadius2
            ))
            ps.push(new THREE.Vector3(
                -sinAmplitude / 2,
                Math.cos(Math.PI * 2 * (i+0.5) / sinPeriods) * sinRadius1,
                Math.sin(Math.PI * 2 * (i+0.5) / sinPeriods) * sinRadius2
            ))
        }

        const spline = new THREE.CatmullRomCurve3(ps, true)
        spline.type = 'centripetal'
        const samples = spline.getPoints( ps.length * 10 )

        console.log(spline.getLength())
        const splineLength = spline.getLength()
        const sphereRadius = 5
        const sphereCount = Math.floor(splineLength / (sphereRadius * 2))
        const spherePositions: Array<THREE.Vector3> = []
        for ( let i = 0; i < sphereCount; i++ ) {
            spherePositions.push(
                spline.getPointAt(i / sphereCount)
            )
        }

        const geometrySpline = new THREE.BufferGeometry().setFromPoints( samples )
        const line = new THREE.Line( geometrySpline, new THREE.LineDashedMaterial( { color: 0xff0000, dashSize: 1, gapSize: 0.5 } ) )
        this.scene.add( line )


        var particleGeometry = new THREE.SphereBufferGeometry(sphereRadius)
        var particleMaterial = new THREE.MeshPhysicalMaterial({
            clearcoat: 2.0,
            clearcoatRoughness: 0.1,
            metalness: 0.8,
            roughness: 0.2,
            sheen: 2,
            sheenColor: new THREE.Color(0xffa050)
            // emissive: new THREE.Color(0xffff80),
            // emissiveIntensity: 0.5
        })
        var particleEngine1 = new PE.ParticleStaticEngine(sphereCount, particleGeometry, particleMaterial,
            (index) => {
                return spherePositions[index]
            },
            (_) => {
                return new THREE.Euler(0,0,0)
            },
            (_) => {
                return 1
            },
            (_) => {
                return new THREE.Color(0xffffe0)
            }
        )
        this.scene.add(particleEngine1.mesh)

        const loader = new FBXLoader(this.loadingManager)
        var ao1Map = new THREE.TextureLoader(this.loadingManager).load( './flower1/lotus/color.jpg' )
        var texMap = new THREE.TextureLoader(this.loadingManager).load( './flower1/bump.jpg' )
        var normMap = new THREE.TextureLoader(this.loadingManager).load( './flower1/normal.png' )
        loader.load( './flower1/lotus/lotus.fbx', (obj) => {
            if (obj instanceof THREE.Group && obj.children[0] instanceof THREE.Mesh) {
                const mesh = obj.children[0]
                mesh.material = new THREE.MeshPhysicalMaterial({
                    color: 0xa0a080,
                    map: ao1Map,
                    // reflectivity: 0.5,
                    normalMap: normMap,
                    normalScale: new THREE.Vector2(0.6),
                    metalness: 0.6,
                    metalnessMap: texMap,
                    // roughnessMap: texMap,
                    clearcoat: 1.0,
                    clearcoatRoughness: 0.4,
                    clearcoatMap: texMap,
                    side: THREE.DoubleSide
                })
                // mesh.rotation.x = Math.PI / 4
                self.scene.add(mesh)
            }
        })

        var kao1Map = new THREE.TextureLoader(this.loadingManager).load( './flower1/kud/kud_1.jpg' )
        var kao2Map = new THREE.TextureLoader(this.loadingManager).load( './flower1/kud/kud_2.jpg' )
        const loader2 = new FBXLoader(this.loadingManager)
        loader2.load( './flower1/kud/kud.fbx', (obj) => {
            if (obj instanceof THREE.Group && obj.children[0] instanceof THREE.Mesh) {
                const mesh = obj.children[0]
                const frontbackMaterials = [
                    new THREE.MeshPhysicalMaterial({
                        color: 0xff8000,
                        map: kao1Map,
                        reflectivity: 0.5,
                        metalness: 0.5,
                        metalnessMap: texMap,
                        // bumpMap: lotusBumpMap,
                        // bumpScale: 10,
                        clearcoat: 1.0,
                        // normalMapType: THREE.ObjectSpaceNormalMap,
                        side: THREE.BackSide
                    }),
                    new THREE.MeshPhysicalMaterial({
                        color: 0x8080ff,
                        map: kao2Map,
                        reflectivity: 0.5,
                        metalness: 0.5,
                        metalnessMap: texMap,
                        // bumpMap: lotusBumpMap,
                        // bumpScale: 10,
                        clearcoat: 1.0,
                        // normalMapType: THREE.ObjectSpaceNormalMap,
                        side: THREE.FrontSide
                    })
                ]
                var frontBackMesh = createMultiMaterialObject( mesh.geometry, frontbackMaterials );
                frontBackMesh.position.x += 200
                // mesh.rotation.x = Math.PI / 4
                self.scene.add(frontBackMesh)
            }
        })

        
        var fao1Map = new THREE.TextureLoader(this.loadingManager).load( './flower1/f3/ao_1.jpg' )
        var fao2Map = new THREE.TextureLoader(this.loadingManager).load( './flower1/f3/ao_21.jpg' )
        const loader3 = new FBXLoader(this.loadingManager)
        loader3.load( './flower1/f3/f3.fbx', (obj) => {
            if (obj instanceof THREE.Group && obj.children[0] instanceof THREE.Mesh) {
                const mesh = obj.children[0]
                const frontbackMaterials = [
                    new THREE.MeshPhysicalMaterial({
                        color: 0xffff20,
                        map: fao1Map,
                        reflectivity: 0.5,
                        metalness: 0.5,
                        metalnessMap: texMap,
                        // bumpMap: lotusBumpMap,
                        // bumpScale: 10,
                        clearcoat: 1.0,
                        // normalMapType: THREE.ObjectSpaceNormalMap,
                        side: THREE.BackSide
                    }),
                    new THREE.MeshPhysicalMaterial({
                        color: 0xff4040,
                        map: fao2Map,
                        reflectivity: 0.5,
                        metalness: 0.5,
                        metalnessMap: texMap,
                        // bumpMap: lotusBumpMap,
                        // bumpScale: 10,
                        clearcoat: 1.0,
                        // normalMapType: THREE.ObjectSpaceNormalMap,
                        side: THREE.FrontSide
                    })
                ]
                var frontBackMesh = createMultiMaterialObject( mesh.geometry, frontbackMaterials );
                frontBackMesh.position.x -= 200
                // mesh.rotation.x = Math.PI / 4
                self.scene.add(frontBackMesh)
            }
        })


        // logo cards
        const logoColorMap = new THREE.TextureLoader(this.loadingManager).load( './logo/logo.jpg' )
        const logoReflectMap = new THREE.TextureLoader(this.loadingManager).load( './logo/reflect.jpg' )
        const logoMaterial = new THREE.MeshPhysicalMaterial({
            map: logoColorMap,
            reflectivity: 10,
            metalnessMap: logoReflectMap,
            // metalness: 0.5,
            // lightMap: logoReflectMap,
            metalness: 2,
            // metalnessMap: logoReflectMap,
            clearcoat: 1.0,
            // specularIntensity: 5.0,
            // clearcoatMap: logoReflectMap,
            side: THREE.DoubleSide
        })
        const logoCardGeometry = new THREE.PlaneBufferGeometry(100, 100)
        var logoCardEngine = new PE.ParticleStaticEngine(10, logoCardGeometry, logoMaterial,
            (index) => {
                return this.getRandomLogoPosition(index, 350)
            },
            (_) => {
                return new THREE.Euler(
                    Math.random() * 0.5 - 0.5,
                    Math.random() * 0.5 - 0.25,
                    Math.random() * 0.2 - 0.1)
            },
            (_) => {
                return 1
            },
            (_) => {
                return new THREE.Color(0xffffff)
            }
        )
        this.scene.add(logoCardEngine.mesh)



        // *********** events ***********
        this.mousePosX = 0
        this.mousePosY = 0
        this.pointer = new THREE.Vector2()
        window.addEventListener( 'resize', () => {
            self.onWindowResize()
        } )
        window.addEventListener( 'click', (e) => {
            self.onClick(e)
        } )

        // *********** final animate ***********
        this.animate()
    }

    animate = () => {
        requestAnimationFrame( this.animate )
        // damping controllers
        // this.updatecornerObject()
        this.render()
        this.stats.update()
        this.controls.update()
    }

    render()
    {
        this.renderer.render(this.scene, this.camera)
    }

    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.renderer.setSize( window.innerWidth, window.innerHeight )
    }
    
    onClick(e: MouseEvent) {
    }

    getRandomLogoPosition(index: number, radius: number = 300): THREE.Vector3 {
        let angle = randn_bm() * 0.8 - Math.PI / 2
        let radiusNew = radius + randn_bm()
        let randomOffset = new THREE.Vector3(randn_bm() * 60, randn_bm() * 5, randn_bm() * 60)
        return new THREE.Vector3(Math.cos(angle), 0, Math.sin(angle)).multiplyScalar(radiusNew).add(randomOffset)
    }

    getCubemapUrls(dir: string, path: Array<string>): Array<string> {
        var urls: Array<string> = []
        path.forEach(p => {
            urls.push(dir + p)
        })
        return urls
    }

}


export {
    BackgroundManager
}